
.middle {
  height: 100%;
  width: 250px;
  background: #e6e6e6;
  overflow-y: scroll;
}
.middle .top-search {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    background: #e6e6e6;
    padding: 10px;
    z-index: 2;
}
.middle .top-search .search-box {
      border: 1px solid #d9d7d6;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.middle .top-search input {
      width: 120px;
      height: 25px;
      outline: none;
      padding: 5px 10px;
      border: none;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.middle .top-search button {
      width: 30px;
      border: none;
      outline: none;
      padding: 0;
      margin: 0;
}
.middle .top-search .clear {
      display: none;
      position: absolute;
      right: 45px;
      top: 14px;
      font-size: 20px;
      background: #e7e7e7;
      width: 20px;
      height: 20px;
      line-height: 20px;
      text-align: center;
      border-radius: 50%;
      cursor: pointer;
}
.middle .top-search .show-clear {
      display: block;
}
.middle .list {
    padding: 0;
    margin: 0;
}
.middle .list .list-item {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      padding: 10px;
      position: relative;
      cursor: pointer;
}
.middle .list .list-item:hover {
        background: #D9d8d8;
}
.middle .list .list-item.current {
        background: #c3c3c4;
}
.middle .list .avatar {
      width: 40px;
      height: 40px;
}
.middle .list .avatar img {
        width: 100%;
        height: 100%;
        border-radius: 5px;
}
.middle .list .user {
      position: relative;
      margin-left: 15px;
      font-size: 14px;
}
.middle .list .user .username {
        width: 100px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.middle .list .user .message {
        width: 155px;
        margin-top: 5px;
        height: 17px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #afa0a8;
        font-size: 12px;
}
.middle .list .user .newmsg-bubble {
        position: absolute;
        right: 0;
        bottom: -2px;
        width: 20px;
        height: 20px;
        line-height: 20px;
        border-radius: 20px;
        color: #fff;
        background: #ff3b30;
        font-size: 12px;
        text-align: center;
}
.middle .list .time {
      position: absolute;
      right: 10px;
      font-size: 12px;
      color: #afa0a8;
}



