
ul {
  list-style: none;
}
.left-bar {
  width: 60px;
  height: 100%;
  background: #25292c;
  color: #8c8c8c;
  cursor: move;
}
.left-bar .avatar {
    width: 40px;
    height: 40px;
    margin: 30px auto;
}
.left-bar .avatar img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
}
.left-bar .rs-nav {
    width: 100%;
    height: 40px;
    margin: 0;
    padding: 0;
    margin-top: 15px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
}
.left-bar .rs-nav .nav-item {
      margin: 0 5px;
      background: #eee;
      border-radius: 5px;
      color: #000;
}
.left-bar .rs-nav a {
      text-decoration: none;
      color: #8c8c8c;
}
.left-bar .rs-nav a.active {
        color: #129611;
}



